import React, { FC } from 'react';
import { useState } from 'react';
import './input.css';

interface InputProps {
    id: string,
    label: string,
    value?: string,
    condition?:{
        
    }
    success?: boolean,
    placeholder: string,
    onChange: (str: string) => void,
    size?: 'large' | 'medium' | 'small';
}



export const Input = ({
    id,
    label = '',
    placeholder = '',
    disabled = false,
    success = false,
    onChange,
    value,
    size = 'medium',
    ...props
}: InputProps) => {



    return (
        <>
            <label className='input--label'>

                {label}
            </label>
            <input
                id={id}
                className={`input--${size}` }
                disabled={disabled}
                onChange={e => onChange(e.target.value)}
                value={value}
                placeholder={placeholder}
                {...props} />
        </>
    )
}

export default Input